<template>
    <div>
        <div class="row">
            <div class="col-12" v-if="ocultar">
                <router-view></router-view>
            </div>

            <div class="col-3" v-if="!ocultar">
                <SideBar />
            </div>
            <div class="col-9" v-if="!ocultar">
                <router-view></router-view>
            </div>           
        </div>
        
    </div>
</template>

<script>
import SideBar from '@/components/Element/SideBar'

export default {
    name: 'List',
    components: {
        SideBar
    },
    data() {
        return {
            ocultar: false
        };
    },
    methods: {
        ocultarFunction() {
            if (this.$route.path == '/persons/'){
                this.ocultar = true
            }else{
                this.ocultar = false
            }
        }
    },
    mounted() {
        this.ocultarFunction()
    },
    watch: {
        "$route.path": function (){
            this.ocultarFunction()
            
        }
    },
};
</script>

<style lang="css" scoped>

</style>


