<template>
    <div>
        <div class="mt-4">                            
            <b-card >
            <b-card-text>
                <div class="text-center">
                    
                    <h4>Buscador de Datos:</h4>
                    <hr>
                </div>
                <form @submit="onSubmit">
                    <div class="row mb-2">
                        <div class="col-sm-4 mb-3">
                            <div class="input-group">
                                <label class="input-group-text" for="inputGroupSelect01">Nro</label>
                                <input type="number" class="col-sm-6 form-control" 
                                v-model="nro" :disabled="success" id="nro" 
                                :class=" {'is-invalid': errores.dni} "
                                />
                                <span class="input-group-addon input-group-addon-btn bg-white">
                                    <div class="" v-if="!success">
                                        <button class="btn px-2 btn-primary" type="submit"
                                        :disabled="isDisabled">
                                            <b-icon-search></b-icon-search> 
                                        </button>
                                    </div>
                                    <div class="" v-else>
                                        <button class="btn px-2 btn-danger" @click.prevent="clearForm">
                                            <b-icon-basket></b-icon-basket> 
                                        </button>
                                    </div>

                                </span>
                            </div>
                            <b-form-text id="password-help-block">
                                <div class="" v-if="errores.dni">
                                    <small class="text-danger">{{ errores.dni[0] }}</small>
                                </div>
                                <div class="" v-else>
                                    Solo estan permitido valores [0-9].

                                </div>
                            </b-form-text>
                        </div>
                        <div class="col-sm-4">
                            <div class="row">
                                <label for="inputEmail3" class="col-sm-9 col-sm-form-label">Cod. Verificacion</label>
                                <div class="col-sm-3">
                                <input type="text" class="form-control" :value="rpta.codigo_verificacion" disabled>
                                </div>

                            </div>
                        </div>
                        <div class="col-sm-2" v-if="success">
                            <button class="btn btn-primary" @click.prevent="sendForm">
                                <b-icon-chevron-double-right></b-icon-chevron-double-right> 
                            </button>
                            <button class="btn btn-secondary">
                                <b-icon-arrow-return-left></b-icon-arrow-return-left>
                            </button>
                        </div>

                        <div class="row mb-3">
                            <label for="inputEmail3" class="col-sm-2 col-sm-form-label">Nombres</label>
                            <div class="col-sm-10">
                            <input type="text" class="form-control" disabled v-model="form.names">
                            </div>
                        </div>
                        <div class="row mb-3">
                            <div class="col-sm-6">
                                <div class="row">
                                    <label for="inputEmail3" class="col-sm-4 col-sm-form-label">Ap. Paterno</label>
                                    <div class="col-sm-8">
                                    <input type="text" class="form-control" disabled v-model="form.last_name_p">
                                    </div>

                                </div>

                            </div>
                            <div class="col-sm-6">
                                <div class="row">
                                    <label for="inputEmail3" class="col-sm-4 col-sm-form-label">Ap. Materno</label>
                                    <div class="col-sm-8">
                                    <input type="text" class="form-control" disabled v-model="form.last_name_m" >
                                    </div>

                                </div>

                            </div>
                        </div>
                        <div class="row mb-3">
                            <div class="col-sm-6">
                                <div class="row">
                                    <label for="inputEmail3" class="col-sm-4 col-sm-form-label">F. Nacimiento</label>
                                    <div class="col-sm-8">
                                    <input type="text" class="form-control" disabled :value="rpta.fecha_nacimiento" >
                                    </div>

                                </div>

                            </div>
                           
                        </div>
                        <div v-if="success">
                            <p>Rpta JSON</p>
                            <p>{{ rpta }}</p>
                            <div class="" v-if="rpta.image">
                                <img :src="server + rpta.image" alt="">
                            </div>
                        </div>
                        <div class="" v-if="error">
                            <p class="text-danger">Aviso!</p>
                            <p>{{ this.message }}</p>
                        </div>
                    </div>
                    
                </form>
            </b-card-text>
            </b-card>
        </div>
    </div>
</template>

<script>
import swal from 'sweetalert'
import axios from 'axios'
axios.defaults.xsrfHeaderName = "X-CSRFTOKEN"
axios.defaults.xsrfCookieName = "csrftoken"

export default {
    name: 'SearchPerson',
    data() {
        return {
            nro: '',
            message: {},
            success: false,
            server: process.env.VUE_APP_SERVER,
            error: false,
            rpta: [],
            errores: {},
            form: {
                dni: '',
                names: '',
                last_name_p: '',
                last_name_m: ''
            }
            
        };
    },
    mounted() {
        this.onFocus()
    },
    methods: {
        onSubmit(evt) {
            evt.preventDefault()
            this.searchLocal(this.nro)
        },
        onFocus (){
            document.getElementById('nro').focus()   
        },
        clearForm() {
            this.nro = ''
            this.success = false
            this.rpta = []
            this.error = false
            // 
            this.form.dni = ''
            this.form.names = ''
            this.form.last_name_p = ''
            this.form.last_name_m = ''
            this.errores = {}
            this.onFocus()  

        },
        searchLocal(dni){
            const path = `${process.env.VUE_APP_BASE_URL}natural/${dni}/search/`

                axios.get(path)
                .then(res => {
                    if (res.data.message){
                        console.log(res.data.message)
                        this.searchRemote(this.nro)
                    }else{
                        // Si existe
                        // console.log(res.data)
                        this.errores.dni = ["DNI ya registrado en BD"]
                        this.success = true
                        this.rpta = res.data[0]
                        // console.log(this.rpta)
                        // this.form.dni = rpta.names
                        this.form.names = this.rpta.names
                        this.form.last_name_p = this.rpta.last_name_p
                        this.form.last_name_m = this.rpta.last_name_m
                    }
                })
                .catch(err => {
                    console.log(err)
                    if( err.response ){
                        // this.errores = err.response.data
                        console.log(err.response)
                        // console.log(this.errores.dni)
                        // alert(this.errores.dni)
                    }
                })
        },
        searchRemote(dni){
            axios.get(`https://apiperu.dev/api/dni/${dni}`, {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer 5f083fea816b90dbfca1ee642aa2abc29c13ba0283a627ba7c6fbaf6dc662774'
                },
            })
            .then(res => {
                if (res.data.success === true){
                    this.rpta =  res.data.data
                    this.error = false
                    this.success = true

                    // Cargar Datos
                    this.form.dni = this.nro
                    this.form.names = this.rpta.nombres
                    this.form.last_name_p = this.rpta.apellido_paterno
                    this.form.last_name_m = this.rpta.apellido_materno
                    
                }else{
                    console.log(res.data)
                    this.error = true
                    this.message = res.data
                }
            })
            .catch(err => {
                console.error(err); 
            })

        },
        sendForm(){
            if(this.success===true){
                const path = `${process.env.VUE_APP_BASE_URL}natural/`

                axios.post(path, this.form)
                .then(res => {
                    console.log(res)
                    swal("Guardado correctamente", "", "success")
                    this.clearForm()
                })
                .catch(err => {
                    if( err.response ){
                        this.errores = err.response.data
                        console.log(this.errores)
                    }
                })
            }
        }
    },
    computed: {
        isDisabled() {
            if(this.nro.indexOf('.') != -1){
                return true
            }else{
                if(this.nro.length === 8 || this.nro.length === 11){
                    return false
                }else{
                    return true
                }
            }
        },
    },
};
</script>

<style lang="css" scoped>

</style>


