import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import ListBook from '@/components/Book/ListBook'
import EditBook from '@/components/Book/EditBook'
import NewBook from '@/components/Book/NewBook'
import DeleteBook from '@/components/Book/DeleteBook'
import List from '@/components/Element/List'
import ListElements from '@/components/Element/ListElements'
import ListElementsCategory from '@/components/Element/ListElementsCategory'
import ListElementsType from '@/components/Element/ListElementsType'
import SearchPerson from '@/components/Person/SearchPerson'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/books',
      name: 'ListBook',
      component: ListBook
    },
    {
      path: '/books/new',
      name: 'NewBook',
      component: NewBook
    },
    {
      path: '/books/:bookId/edit',
      name: 'EditBook',
      component: EditBook
    },
    {
      path: '/books/:bookId/delete',
      name: 'DeleteBook',
      component: DeleteBook
    },
    {
      path: '/elements',
      component: List,
      children: [
        {
          path: '',
          name: 'elements',
          component: ListElements
        },
        {
          path: 'type/:id/',
          name: 'type',
          component: ListElementsType
        },
        {
          path: 'category/:id/',
          name: 'category',
          component: ListElementsCategory
        }
    ]
    },
    {
      path: '/persons',
      component: List,
      children: [
        {
          path: '',
          name: 'persons',
          component: SearchPerson
        },
    ]
    },
  ],
  mode: 'history',
})



