<template>
    <div>
        <!-- <div class="container"> -->
        <h2 class="text-center">{{ message }}</h2>
        
        
        <div v-if="validation">
            <h5 class="text-danger">
                {{ validation }}

            </h5>
        </div>
        <div class="" v-else>
            
            <b-card-group deck>
                <b-card
                class="mb-2"
                v-for="(item, index) in books"
                :key="item.id"
                :header= "'Item N° ' + (index + 1)"
                :title="item.description"
                >
                <b-card-text>
                    Type: <strong>{{ item.type }}</strong><br>
                    Category: <strong>{{ item.category }}</strong><br>
                </b-card-text>
                <b-button href="#" size="sm" variant="primary">Detalles</b-button>
                </b-card>

            </b-card-group>
        </div>                        

        
        <!-- </div> -->
        
    </div>
</template>

<script>
import SideBar from '@/components/Element/SideBar'

export default {
    name: 'ListDefault',
    components: {
        SideBar,
    },
    props: {
        message: String,
        // books: Array,
        validation: {
            type: String,
            default: ''
        },
        books: {
            type: Array,
            default: []
        }
    },
    data() {
        return {
        };
    },
};
</script>

<style lang="css" scoped>

</style>


