<template>
    <div>
        <b-navbar type="dark" variant="dark" class="px-2">
            <b-navbar-nav>
            <b-navbar-brand href="#">
                <b-icon-tools></b-icon-tools> Django-Vue
            </b-navbar-brand>

            <router-link :to="{ name: 'ListBook' }" custom v-slot="{ href, navigate, isActive }">
                <b-nav-item :active="isActive" :href="href" @click="navigate">
                    Books
                </b-nav-item> 
            </router-link>

            <router-link :to="{name: 'elements'}" custom v-slot="{ href, navigate, isExactActive }">
                <b-nav-item :active="isExactActive" :href="href" @click="navigate">
                    Elements
                </b-nav-item> 
            </router-link>

            <!-- Navbar dropdowns -->
            <b-nav-item-dropdown text="Types" left>
                <router-link v-for="item in types" :key="item.id" :to="{ name: 'type', params: {id : item.id }}" 
                custom v-slot="{ href, navigate, isExactActive }">
                    <b-dropdown-item :active="isExactActive" :href="href"  @click="navigate">
                        {{ item.description }}
                    </b-dropdown-item>
                </router-link>

            </b-nav-item-dropdown>
            
            <!-- Navbar dropdowns -->
            <b-nav-item-dropdown text="Categorys" left>
                <router-link v-for="item in categorys" :key="item.id" :to="{ name: 'category', params: {id : item.id }}" 
                custom v-slot="{ href, navigate, isExactActive }">
                    <b-dropdown-item :active="isExactActive" :href="href"  @click="navigate">
                        {{ item.description }}
                    </b-dropdown-item>
                </router-link>
            </b-nav-item-dropdown>

            <router-link :to="{name: 'persons'}" custom v-slot="{ href, navigate, isExactActive }">
                <b-nav-item :active="isExactActive" :href="href" @click="navigate">
                    Persons
                </b-nav-item> 
            </router-link>

            </b-navbar-nav>
        </b-navbar>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'NavBar',
    data() {
        return {
            types: [],
            categorys: []
        };
    },
    mounted() {
        
    },
    methods: {
        findTypes() {
            axios.get(`${process.env.VUE_APP_BASE_URL}types/`)
            .then(res => {
                this.types =res.data
            })
            .catch(err => {
                console.error(err); 
            })
        },
        findCategorys() {
            axios.get(`${process.env.VUE_APP_BASE_URL}categorys/`)
            .then(res => {
                this.categorys =res.data
            })
            .catch(err => {
                console.error(err); 
            })
        },
        
    },
    created() {
        this.findTypes()
        this.findCategorys()
    },
};
</script>

<style lang="css" scoped>

</style>


