<template>
    <div>
        <div class="text-center">
            <div class="">
                <h3>Tipos</h3>
                
                <b-list-group>                    
                    <router-link v-for="item in types" :key="item.id"
                    :to="{ name: 'type', params: {id : item.id }}" custom v-slot="{ href, navigate, isExactActive }">
                        
                        <b-list-group-item :active="isExactActive" :href="href" @click="navigate">
                            {{item.description}}
                        </b-list-group-item>
                        
                    </router-link>
                </b-list-group>
            </div>
            <div class="mt-2">
                <h3>Categorias</h3>
                <b-list-group>                    
                    <router-link v-for="item in categorys" :key="item.id"
                    :to="{ name: 'category', params: {id : item.id }}" custom v-slot="{ href, navigate, isExactActive }">
                        
                        <b-list-group-item :active="isExactActive" :href="href" @click="navigate">
                            {{item.description}}
                        </b-list-group-item>
                        
                    </router-link>
                </b-list-group>
            </div>

        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'SideBar',
    data() {
        return {
            types: [],
            categorys: []
        };
    },
    mounted() {
        
    },
    methods: {
        findTypes() {
            axios.get(`${process.env.VUE_APP_BASE_URL}types/`)
            .then(res => {
                this.types =res.data
            })
            .catch(err => {
                console.error(err); 
            })
        },
        findCategorys() {
            axios.get(`${process.env.VUE_APP_BASE_URL}categorys/`)
            .then(res => {
                this.categorys =res.data
            })
            .catch(err => {
                console.error(err); 
            })
        },
        
    },
    created() {
        this.findTypes()
        this.findCategorys()
    },
};
</script>

<style lang="css" scoped>

</style>


