<template>
    <div>
        
        <ListDefault :books="this.books" :message="this.message" :validation="this.validation"/>
        
    </div>
</template>

<script>
import axios from 'axios'
import ListDefault from '@/components/Element/partials/ListDefault'

export default {
    name: 'ListElementsType',
    components: {
        ListDefault,
    },
    data() {
        return {
            books: [],
            message: '',
            validation: ''
        };
    },
    mounted() {
        
    },
    methods: {
        getBooks() {
            // console.log(this.$route.params.id)
            const path = `${process.env.VUE_APP_BASE_URL}types/${this.$route.params.id}/elements/`
            axios.get(path)
            .then(res => {
                this.books = res.data
                this.message = `Elementos de tipo ${this.$route.params.id}`
                this.validation = ''

                if (res.data.message){
                    this.books = []
                    this.validation = res.data.message
                }
                
            })
            .catch(err => {
                console.error(err); 
            })
        }
    },
    created() {
        this.getBooks()
    },
    watch: {
        "$route.params.id": function (){
            this.getBooks()
        }
    },
};
</script>

<style lang="css" scoped>

</style>


