<template>
    <div>
        <ListDefault :books="books" :message="message"/>
        
    </div>
</template>

<script>
import axios from 'axios'
import ListDefault from '@/components/Element/partials/ListDefault'

export default {
    name: 'ListElements',
    components: {
        ListDefault
    },
    data() {
        return {
            books: [],
            message: ''
        };
    },
    mounted() {
        
    },
    methods: {
        getBooks() {
            const path = `${process.env.VUE_APP_BASE_URL}elements/`
            axios.get(path)
            .then(res => {
                this.books = res.data
                this.message = `Listado de Elementos (${this.books.length})`
            })
            .catch(err => {
                console.error(err); 
            })
        }
    },
    created() {
        this.getBooks()
    },
};
</script>

<style lang="css" scoped>

</style>


